import { CUCalendarJob, LoginRequest } from '../models/login-request';
import { StorageVars } from '../models/storage-vars';
import { Subscription } from '../models/subscription.model';
import { ForgotPasswordComponent } from './forgot-password-dialog/forgot-password.component';
import { Component, AfterContentInit, Inject, Renderer2, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
